
.food {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 48px;
  width: 100%;
  background-color: #fff;
}
.food.slide-left-enter,
.food.slide-left-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.food.slide-left-enter-active,
.food.slide-left-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.food .food-header {
  position: relative;
}
.food .food-header .img-wrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .food-header .img-wrapper img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .food-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .food-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .food-header .content {
  position: relative;
  padding: 18px;
}
.food .food-header .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .food-header .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.food .food-header .content .detail .sell-count,
.food .food-header .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .food-header .content .detail .sell-count {
  margin-right: 12px;
}
.food .food-header .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .food-header .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food .food-header .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food .food-header .content .count-wrapper {
  position: absolute;
  right: 18px;
  bottom: 15px;
}
.food .food-header .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  font-size: 10px;
  color: #fff;
  background: #00a0dc;
  opacity: 1;
}
.food .food-header .content .buy.fade-enter-active,
.food .food-header .content .buy.fade-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.food .food-header .content .buy.fade-enter,
.food .food-header .content .buy.fade-leave-active {
  opacity: 0;
  z-index: -1;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.food .food-ratings {
  padding-top: 18px;
}
.food .food-ratings .title {
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
  padding-left: 18px;
}
.food .ratings-wrapper {
  margin: 0 18px;
}
.food .ratings-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .ratings-wrapper .rating-item:after {
  content: ' ';
  display: block;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.food .ratings-wrapper .rating-item .time {
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  margin-bottom: 6px;
}
.food .ratings-wrapper .rating-item .content {
  font-size: 0;
}
.food .ratings-wrapper .rating-item .content .icon-thumb_up,
.food .ratings-wrapper .rating-item .content .icon-thumb_down {
  font-size: 12px;
  line-height: 16px;
  margin-right: 4px;
}
.food .ratings-wrapper .rating-item .content .icon-thumb_up {
  color: #00a0dc;
}
.food .ratings-wrapper .rating-item .content .icon-thumb_down {
  color: #93999f;
}
.food .ratings-wrapper .rating-item .content .text {
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}
.food .ratings-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 0;
}
.food .ratings-wrapper .rating-item .user .name {
  display: inline-block;
  margin-right: 6px;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
}
.food .ratings-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}


